@import '~antd/dist/antd.css';
/* * {
  font-size: 18px;
} */
.App {
  text-align: center;
}
.title {
  display: block;
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background: #f0f0f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.form-card {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  width: 800px;
  height: auto;
  overflow: scroll;
  background: white;
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.2);
}
.login-form {
  position: relative;
  width: 100%;
}
